import Loading from '../../public/Loading'
import Head from '../../public/head.vue'
import None from '../../public/noNumber'
  export default {
      name: "publishList",
    data(){
        return{
          api: this.GLOBAL.baseURL,
          publishList:[],
          loadingState:false,
          user:'',
          activeName:'2',
          audit_face:'',
          isLook:false,
          idCard:'',
          name: '',
          isLoading:false,
          refuse_reason:''
        }
    },
    components:{
      Head,
      Loading,
      None
    },
    methods:{
      handleClick(val){
        this.getPublish(this.activeName)
      },
      toExperience(){
        this.$router.push('/experience')
      },
      deelActive(item,index){
        this.$http.post(this.api + '/home/Activity/del_activity',{
          token: localStorage.getItem('token'),
          activity_id: item.active_id
        })
          .then(res=>{
            if(res.data.code == 1){
              this.$message({
                type: 'success',
                message: '删除成功!'
              })
              this.getPublish()
            }else if(res.data.code == 3){
              alert(res.data.msg)
            }else{
              this.$alert(res.data.msg)
            }
          })
      },
      active(item,index){
        this.$router.push({
          name: 'Experience',
          query:{
            information: item.active_id,
            complete: item.complete
          }
        })
      },
      abolish(item,index){
        item.visible = false
      },
      getPublish(){
        this.$http.post(this.api + '/home/Activity/complete',{
          token:localStorage.getItem('token'),
          flag:this.activeName
        })
          .then(res=>{
            if(res.data.code == 1){
              let data = res.data.data
              let a = []
              for(let i = 0;i<data.length;i++){
                if(data[i].cover_image){
                  a.push({
                    backUrl: data[i].cover.domain + data[i].cover.themb_url,
                    complete:data[i].complete,
                    active_id: data[i].activity_id,
                    title:data[i].title,
                    step:15-data[i].step.replace(/[^0-9]/ig,' ').trim().split(/\s+/).length,
                    audit: data[i].audit,
                    status: data[i].status,
                    visible:false,
                    reason: data[i].reason
                  })
                }else{
                  a.push({
                    backUrl: '../../../static/img/static/defult.png',
                    complete:data[i].complete,
                    active_id: data[i].activity_id,
                    title:data[i].title,
                    step:15-data[i].step.replace(/[^0-9]/ig,' ').trim().split(/\s+/).length,
                    audit: data[i].audit,
                    status: data[i].status,
                    visible:false,
                    reason: data[i].reason
                  })
                }
            }
              this.publishList = a
            }else if(res.data.code == 3){
              this.$http.post(this.api + '/home/index/token')
                .then(res=>{
                  localStorage.setItem('token',res.data.data)
                  this.getPublish()
                })
            }
          })
      },
      tocheck(){
        this.$router.push('/authentication')
      },
      getUser(){
        this.$http.post(this.api + '/home/User/get_user',{
          token: localStorage.getItem('token')
        })
          .then(res=>{
            if(res.data.code == 1){
              console.log(res)
              let data = res.data.data[0]
              this.audit_face =  data.audit_face
              this.name = data.family_name + data.middle_name + data.name
              this.idCard = data.idcard_n.slice(0,5)
              this.refuse_reason = data.refuse_reason
              this.isLoading = false
            }else if(res.data.code == 3){
              this.$http.post(this.api + '/home/index/token')
                .then(res=>{
                  localStorage.setItem('token',res.data.data)
                  this.getUser()
                })
            }else if(res.data.code == 0){
              alert(res.data.msg)
            }
          })
      },
      lookYan(){
        this.isLook = !this.isLook
      }
    },
    mounted(){
        localStorage.removeItem('active_id')
        let _this = this
      _this.isLoading = true
      _this.handleClick()
      _this.getUser()
      if(this.$route.query.information){
        this.activeName = this.$route.query.information
      }

    }
  }



